.rfm-marquee-container{
  width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
}

:root {
  --base-font-family: "Rubik", sans-serif;
  --base-font-style: normal;
  --header-height: 16vh;
  --top-header-height: 60%;
  --main-header-height: 40%;
  --logo-height: 8vh;
  --top-header-background-color: #3c3a3a;
  --main-header-background-color: #3c3a3a;
  /* --top-header-background-image: url("../public/9M\ Top\ header\ _page-0001.jpg");
  --main-header-background-image: url("../public/b.jfif");
  --footer-background-image: url("../public/9M\ Footer\ faded.png"); */
  --banner-image: url("./Images/Albert_einstein_by_zuzahin-d5pcbug-WikiCommons.jpg")
  --header-font-size: 14px;
  --header-font-color: white;
  --header-font-color-hover: white;
  --header-font-background-color-hover: #e51616;
  --header-dropdown-background-color: #f3f3f3;
  --header-dropdown-item-background-color-hover: #e51616;
  --header-dropdown-font-color: black;
  --header-dropdown-font-color-hover: white;
  --footer-height: 50vh;
  --footer-background-color: #032B5C;
  --footer-font-color: white;
  --footer-font-color-hover: #813da4;
  --footer-font-background-color-hover: white;

  --footer-title-font-size: 18px;
  --bottom-margin-between-footer-content: 25px;
  --footer-content-font-size: 14px;

  --page-margin-left: 120px;
  --page-margin-right: 120px;
  --page-margin-bottom: 0;
  --page-margin-top: var(--header-height);
  --page-padding-top: 2vh;
  --page-padding-bottom: 2vh;
  --page-title-font-size: 23px;
  --page-title-font-color: black;
  --page-title-font-weight: bold;
  --page-title-text-align: center;
  --page-title-margin-bottom: 10px;
  --page-background-color: white;
  /* --page-background-path-image: url("../public/bg_image_3.jpeg"); */
  --page-min-height: calc(
    100vh -
      calc(
        var(--header-height) + var(--footer-height) + var(--page-padding-top) +
          var(--page-padding-bottom)
      )
  );
  --buttons-background-color: green;
  --button2-background-color: #e51616;
  --button3-background-color: #0000ff;
  --buttons-background-color-hover: rgb(138, 244, 159);
  --buttons-title-background-color: white;
  --buttons-title-background-color-hover: black;
  --no-details-available-text-font-size: 16px;
  --no-details-available-text-align: center;
  --page-subTitle-font-size: 16px;
  --page-subTitle-font-color: black;
  --page-subTitle-font-weight: bold;
  --page-subTitle-text-align: left;

  --bottom-margin-between-contents: 30px;

  --page-content-font-size: 14px;
  --page-content-font-color: black;
  --page-content-font-weight: 400;
  --page-content-text-align: left;
  --page-content-margin-left: 70px;
  --page-content-margin-right: 70px;

  --page-content-not-found-margin-top: 1rem;
  --page-content-not-found-margin-bottom: 0rem;
  --page-content-not-found-text-align: center;
  --page-content-not-found-font-size: 12px;
  --page-content-not-fount-font-color: black;
  --page-content-not-found-font-weight: 400;
  --page-validation-font-size: 12px;
  --page-validation-padding-left: 11px;

  --form-label-font-size: 13px;
  --form-label-font-color: black;
  --form-label-font-weight: bold;

  --horizontal-line-thickness: 2px;
  --horizontal-line-background-color: #e6e6e8;
  --horizontal-line-bottom-margin: 5px;

  --vertical-line-thickness: 2px;
  --vertical-line-background-color: #e6e6e8;

  --horizontal-line-drawer-color: black;
  --horizontal-line-drawer-margin: 0.5rem 0;

  --transparent-background: transparent;
}
iframe {
  border-radius: 20px;
}
.ant-tabs .ant-tabs-tab.ant-tabs-tab-active {
  font-size: var(--page-subTitle-font-size) !important;
}
.ant-tabs .ant-tabs-tab {
  font-size: var(--page-subTitle-font-size) !important;
  padding: 0px 12px 4px 0px !important;
}

