/*
Added by - Jhamman lal sahu on 07-09-2024
Reason - added css for 1200px
*/
@media screen and (max-width: 1200px) {
  :root {
    --header-height: 20vh;
    --footer-height: 10vh;
    --page-min-height: 76vh;
    --page-margin-left: 70px;
    --page-margin-right: 70px;

    --top-header-height: 75%;
    --main-header-height: 25%;

    --banner-height: 56vh;
  }
  :where(.css-dev-only-do-not-override-d2lrxs).ant-drawer .ant-drawer-body {
    background: white;
  }
  :where(.css-dev-only-do-not-override-d2lrxs).ant-drawer .ant-drawer-header {
    padding: 13px 0;
    background: var(--main-header-background-color);
  }
  :where(.css-dev-only-do-not-override-d2lrxs).ant-drawer .ant-drawer-body {
    padding: 0px;
  }
  .ant-drawer-title {
    color: #fff;
    width: 50vw;
  }
  .ant-drawer-header-title {
    color: #000;
  }
  .ant-drawer-body {
    background-color: #fff;
    color: #000000;
    text-align: left;
  }
  .anticon {
    color: var(--header-font-color);
  }
}
/**
End of the addition by - Jhamman lal sahu on 07-09-2024
Reason - added css for 1200px
*/

