.main_page {
  font-family: Arial, sans-serif;
  /* padding: 20px; */
  background-color: white;
}

/* 1. Banner */
.banner {
    height: 47vh;
    margin-bottom: var(--bottom-margin-between-contents);
    margin-left: calc(var(--page-margin-left) * -1);
    /* margin-top: calc(var(--page-padding-top) * -1); */
    object-fit: fill;
    width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
    position: relative;
}

.banner_image {
  width: 100%;
  height: 47vh;
   display: block;
}

.banner_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.heading {
  font-family: 'Georgia', serif;
    align-items: center;
    color: blue;
}
.tableContent{
  font-family: 'Georgia', serif;
  font-size: 1rem;
  margin-bottom: 40px;
}

table {
      width: 80%;
      margin: auto;
      border-collapse: collapse;
      box-shadow: 0 0 10px rgba(0,0,0,0.1);
    }

    th {
      background-color: darkblue;
      color: white;
      padding: 15px;
      border: 1px solid black;
      text-align: center;
    }

    td {
      padding: 12px 16px;
      border: 1px solid black;
      background-color: rgba(255, 255, 255, 0.9);
    }

    .separator {
  border: none;
  border-top: 1px solid black;
  margin: 1rem auto 2rem;
  width: 100%;
}
    