.main_page {
  font-family: Arial, sans-serif;
  /* padding: 20px; */
  background-color: white;
}

/* 1. Banner */
.banner {
    /* height: var(--banner-height); */
    height: auto;
    /* margin-bottom: var(--bottom-margin-between-contents); */
    margin-left: calc(var(--page-margin-left) * -1);
    /* margin-top: calc(var(--page-padding-top) * -1); */
    object-fit: fill;
    width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
    position: relative;
}

.timerWrapper {
  position: absolute;
  bottom: 10px;      /* adjust as needed */
  left: 50%;
  transform: translateX(-50%);
  z-index: 2;     /* keep it above the banner image */
}

.banner_image {
  width: 100%;
  height: 750px;
  display: block;
}

.banner_logo {
  width: 100%;
  height: 100px;
  display: block;
}

.banner_text {
  position: absolute;
  top: 46%;
  left: 50%;
  transform: translate(-50%, -50%);

  font-size: 2rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);

   max-width: 1000px;


  
}

.title_line {
    color: yellow;
  font-size: 2rem;
  font-weight: bold;
  letter-spacing: 2px;
}

.on_line {
  font-size: 1.5rem;
  margin: 10px 0;
  color: yellow;
}

.main_title {
  font-size: 2.5rem;
  font-weight: bold;
  color: yellow;
  white-space: nowrap;
}

.red_text {
  color: red;
  font-size: 1.3rem;
  font-weight: bold;
  margin: 10px 0;
}

.date {
  color: yellow;
  font-size: 1.2rem;
}

.org {
  font-size: 1.5rem;
  font-weight: bold;
  color: yellow;
}

.location {
  font-size: 1.6rem;
  font-weight: bold;
  color: yellow;
  margin-top: 20px;
}

.info_section {
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
}

.info_section .left {
    font-family: 'Georgia', serif;
      /* margin: 50px; */
      color: #000;
      line-height: 1.6;
  flex: 0 0 60%;
}

.info_section .right {
  flex: 0 0 40%;
}


.dates_card {
  background-color: darkblue;
  color: #ffffff;
  padding: 30px 25px;
  border-radius: 16px;
  /* width: 100%; */
  max-width: 600px;
  font-family: 'Segoe UI', sans-serif;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.15);
}

.date_item {
  padding: 10px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
}

.date_item:last-child {
  border-bottom: none;
}

.date_item h4 {
  color: #ff9966;
  font-size: 1.1rem;
  margin-top: 0px;
  margin-bottom: 4px;
  font-weight: 600;
}

.date_item p {
  margin: 0;
  font-size: 1rem;
  color: #ffffff;
}

.date_item sup {
  font-size: 0.7em;
  vertical-align: super;
}

.about_section {
  display: flex;
  gap: 20px;
  margin-bottom: 40px;
}

.important_date{
/* font-size: 1.8rem; */
 font-family: 'Georgia', serif;
  color: blue;
  font-weight: 700;
  letter-spacing: 0.1em;
  margin-bottom: 20px;
}

.about_the{
font-size: 1.8rem;
  color: blue;
