  letter-spacing: 0.1em;
  margin-bottom: 20px;
}
.about_the_institute{
font-size: 1.8rem;
  color:blue;
  font-weight: 700;
  letter-spacing: 0.1em;
  margin-bottom: 20px;
  margin-top: 0px;
}

.publication_title{
  font-family: 'Georgia', serif;
    font-size: 1.8rem;
  color: blue;
  font-weight: 700;
  letter-spacing: 0.1em;
  margin-bottom: 20px;
  align-items: center;
}

.about_section .left {
  flex: 0 0 50%;
   font-family: 'Georgia', serif;
      /* margin: 50px; */
      color: #000;
      line-height: 1.6;
  flex: 0 0 60%;
}

.about_section .right {
  flex: 0 0 40%;
}

.college_image {
  width: 100%;
  height: auto;
  /* border-radius: 10px; */
}

.publication_section {
  background-color: #80FFFF; /* softer alternative */
  border-radius: 12px;
  margin-bottom: 40px;
   font-family: 'Georgia', serif;
      /* margin: 50px; */
      color: #000;
      line-height: 1.6;
  flex: 0 0 70%;
}

