  font-size: 1.8rem;
  color: #a30059;
  font-weight: 700;
  letter-spacing: 0.1em;
  margin-bottom: 20px;
} */

.contact_us_icon {
  font-family: 'Georgia', serif;
font-size: 1.8rem;
  color: blue;
  font-weight: 700;
  letter-spacing: 0.1em;
  margin-bottom: 20px;
}

.publication_content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  /* align-items: center; */
  /* gap: 10px; */
  flex-wrap: wrap;
  padding-left: 30px;
  padding-right: 30px;
}

.publication_text {
  flex: 1;
  font-size: 1.1rem;
  color: #111;
}

.publication_text strong {
  color: #003366;
}

.paper_submission_list {
  list-style: none; /* Remove default bullets */
  padding: 0;
}

.paper_submission_list li {
  margin-bottom: 10px; /* Spacing between items */
}

.paper_submission_logo{
  max-width: 250px;
  /* height: auto; */
}

.checkmark {
  color: blue;
  margin-right: 10px;
}
.ieee_logo_section{
  display: flex;
  /* align-items: flex-end; */
    justify-content: flex-end;
}

.ieee_logo {
  max-width: 250px;
  height: auto;
  /* justify-content: flex-end; */
}

.CMT_logo {
  width: 200px;
  height: 200px;
}

.cards_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: 40px;
}

.card {
  width: 280px;
  background-color: #ffffff;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease;
}

.card:hover {
  transform: translateY(-8px);
}

.cardImage {
  width: 100%;
  aspect-ratio: 1 / 1;
  overflow: hidden;
  border-bottom: 1px solid #ddd;
}

.cardImage img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.card p {
  margin: 12px 16px;
  font-size: 0.95rem;
  color: #333;
  line-height: 1.4;
  
}

.card p:first-of-type {
  font-weight: 600;
  font-size: 1rem;
  color: #2b2b2b;
  margin-bottom: 4px;
}


/* 6. Schedule Section */
.schedule_section {
  margin-bottom: 40px;
}

.schedule_section ul {
  list-style-type: circle;
  padding-left: 20px;
}

.contact_section {
  margin-bottom: 40px;
}

.contact_with_map{
display: flex;
flex-direction: row;
gap: 2%;
}

.contact_detail{
 flex: 0 0 38%;
 /* padding-right: 20px; */
}

.map {
  margin-top: 20px;
  border: none;
  border-radius: 10px;
  flex: 0 0 60%;
}

.customMarquee {
  display: flex;
  background-color: yellow;
  color: red;
  font-weight: bold;
  padding: 10px;
  font-size: 1.2rem;
   /* margin-bottom: var(--bottom-margin-between-contents); */
    margin-left: calc(var(--page-margin-left) * -1);
    width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
}


.marqueeImage {
  width: 30px; /* Adjust the size as needed */
  height: auto;
  margin-right: 10px;
}

.member_list{
  display: flex;
  flex-direction: column;
  gap: 5%;
}

.member{
  border-bottom: 1px solid black;
}

.member_list > * {
  border-bottom: 1px solid #ccc; 
  padding-bottom: 10px;         
  margin-bottom: 10px;          
}


.member_name{
  color: blue;
}



/* Responsive Design */
@media (max-width: 768px) {
  .info_section,
  .about_section {
    flex-direction: column;
  }

  .cards_container {
    flex-direction: column;
  }
}
