.top_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #054D9F;
  border-bottom: 1px solid #ccc;
  height: var(--top-header-height);
}
.logo_container {
  display: flex;
  align-items: center;
}

.logos {
  display: flex;
  gap: 10px;
  margin-right: 15px;
}

.logo_img {
  height: 65px;
  width: auto;
}

.title {
  font-size: 1.5rem;
  font-weight: bold;
  color: white;
}

.header_buttons .header_btn {
  margin-left: 15px;
  padding: 8px 12px;
  background-color: #FF5252;
  color: white;
  border: none;
  border-radius: 4px;
  text-decoration: none;
}

.header_buttons :hover{
  background-color: #e91e63;
}
