.countdownContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 5px;
  border-radius: 12px;
  gap: 100px;
}

.timeBox {
  text-align: center;
}

.timeBox h1 {
  font-size: 3rem;
  font-weight: bold;
  color: #facc15; /* Yellow */
  margin: 0;
}

.timeBox p {
  font-size: 1rem;
  font-weight: 500;
  color: #facc15; /* Yellow */
  margin: 0;
}
