.main_page {
  font-family: Arial, sans-serif;
  /* padding: 20px; */
  background-color: white;
}
.banner {
    height: 47vh;
    margin-bottom: var(--bottom-margin-between-contents);
    margin-left: calc(var(--page-margin-left) * -1);
    /* margin-top: calc(var(--page-padding-top) * -1); */
    object-fit: fill;
    width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
    position: relative;
}

.banner_image {
  width: 100%;
  height: 47vh;
   display: block;
}

.banner_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.keyNote_speakers{
    display: flex;
    flex-direction: column;
}

.keyNote_speakers_title{
    font-family: 'Georgia', serif;
    color: blue;
    align-items: center;
    justify-content: center;
    display: flex;
}

.spekers{
    display: flex;
    margin-bottom: 40px;
    gap: 5%;
    align-items: center;
    justify-content: center;
}

.speakers_info{
    font-size: 30px;
}

.speakers_image{
    aspect-ratio: 16 / 9;
  width: 450px;
  object-fit: cover; /* or contain / fill based on your goal */
  display: block;
}