.footer {
  display: flex;
  flex-direction: column;
  /* height: var(--footer-height); */
  background-color: var(--footer-background-color);
  color: var( --footer-font-color);
}

.footer_content {
  display: flex;
  justify-content: flex-start;
  margin-right: 80px;
  margin-left: 80px;
}

.footer_headings{
  font-size: var(--footer-title-font-size);
}

.linksContainer {
  display: flex;
  justify-content:flex-start;
  flex-wrap: wrap;
  width: 50%;
}

.linkSection {
  flex: 1;
  /* min-width: 200px; */
  margin: 20px;
}

.links {
  font-size: var(--footer-content-font-size);
}

 .contactSection {
  flex: 1;
  min-width: 200px;
  margin: 20px;
  display: flex;
  flex-direction: column;
}

.linkSection h3, .contactSection h3 {
  position: relative;
  padding-bottom: 10px;
  margin-bottom: 10px;
  color: white; /* Example text color */
}

.linkSection h3::after, .contactSection h3::after {
  content: '';
  display: block;
  width: 50px; /* Length of the horizontal line */
  height: 2px; /* Thickness of the horizontal line */
  background-color: #FF5252; /* Color of the horizontal line */
  margin-top: 8px; /* Space between heading and line */
}

.linkSection ul {
  list-style: none;
  padding: 0;
}

.linkSection ul li {
  position: relative;
  padding-left: 20px; /* Space for the vertical line */
  margin: 8px 0;
}


.linkSection ul li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 2px; 
  height: 100%;
  background-color: #FF5252; /* Color of vertical line */
}


.linkSection ul li a {
  color: white;
  text-decoration: none;
}

.linkSection ul li a:hover {
  text-decoration: underline;
  color: #FF5252;
}

.customEmailLink {
  color: #FF5252; /* Set your desired color */
  text-decoration: none; /* Optional: remove underline */
}

.customEmailLink:hover {
  text-decoration: underline; /* Optional: show underline on hover */
}

.contactSection p {
  margin: 8px 0;
}

.member_list{
  display: flex;
  flex-wrap: wrap;
  gap: 5%;
  font-size: var(--footer-content-font-size);
}

.copyRight {
  text-align: center;
  padding: 10px;
  font-size: 0.9rem;
  color: white;
}


