.main_page {
  font-family: Arial, sans-serif;
  /* padding: 20px; */
  background-color: white;
}

/* 1. Banner */
.banner {
    height: 47vh;
    margin-bottom: var(--bottom-margin-between-contents);
    margin-left: calc(var(--page-margin-left) * -1);
    /* margin-top: calc(var(--page-padding-top) * -1); */
    object-fit: fill;
    width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
    position: relative;
}

.banner_image {
  width: 100%;
  height: 47vh;
   display: block;
}

.banner_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.container { 
  font-family: 'Georgia', serif;
      color: #000;
      line-height: 1.6;
      margin-bottom: 40px;
}

.heading {
  color: blue;
  font-weight: bold;
  margin-bottom: 20px;
}

.separator {
  border: none;
  border-top: 1px solid black;
  margin: 1rem auto 2rem;
  width: 100%;
}

.address{
text-align: center;
  font-family: 'Georgia', serif;
  background-size: cover;

  border-radius: 12px;
  margin: 40px auto;
  width: fit-content;
  color: #000;
  line-height: 1.8;
}

.address p {
  margin: 8px 0;
  font-size: 18px;
}

.map {
  margin-top: 20px;
  border: none;
  border-radius: 10px;
  flex: 0 0 60%;
}