.main_page {
  font-family: Arial, sans-serif;
  background-color: white;
}

.banner {
  height: 47vh;
  margin-bottom: 20px;
  margin-left: calc(var(--page-margin-left) * -1);
  object-fit: fill;
  width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
  position: relative;
}

.banner_image {
  width: 100%;
  height: 47vh;
  display: block;
}

.banner_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.heading {
  color: blue;
  font-weight: bold;
  font-family: 'Georgia', serif;
  margin: 20px 0;
}

.registration_container {
  font-family: Arial, sans-serif;
  /* margin: 20px; */
}

.instruction_list{
  list-style: none;
   padding-left: 0;  /* Removes left padding */
  margin-left: 0;   /* Removes left margin */
}

.instruction_list li {
  margin-bottom: 10px;
}

/* .checkmark {
  color: #e91e63;
  margin-right: 10px;
} */

.checkmark {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  border: 1.5px solid blue;
  border-radius: 50%;
  font-size: 10px;
  margin-right: 8px;
}



.table,
.registration-table,
.feeTable {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid black;
}

.table th,
.table td,
.registration-table th,
.registration-table td,
.feeTable th,
.feeTable td {
  padding: 12px;
  border: 1px solid black;
  text-align: left;
}

.tableRow:nth-child(even),
.registration-table tr:nth-child(even),
.feeTable tr:nth-child(even) {
  background-color: #f9f9f9;
}

.tableRow:hover,
.registration-table tr:hover,
.feeTable tr:hover {
  background-color: #f1f1f1;
}

.labelCell {
  font-weight: bold;
  width: 30%;
}

.account_detail_container {
  /* margin: 20px; */
}

.process_list {
  list-style: none;
  padding-left: 0;
}

.process_list_item {
  margin: 15px 0;
  font-size: 1rem;
}

.checkmark {
  font-size: 1.2rem;
  color: blue;
}

.registration_button {
  display: inline-block;
  background-color: #0033a0;
  color: white;
  padding: 10px 20px;
  text-decoration: none;
  font-weight: bold;
  border-radius: 6px;
  margin-top: 20px;
}

.registration_button:hover {
  background-color: #002080;
}

.bold_text {
  font-weight: bold;
}

.red_text {
  color: #e91e63;
}

.blue_text {
  color: #007bff;
}


