.main_page {
  font-family: Arial, sans-serif;
  /* padding: 20px; */
  background-color: white;
}

/* 1. Banner */
.banner {
    height: 47vh;
    margin-bottom: var(--bottom-margin-between-contents);
    margin-left: calc(var(--page-margin-left) * -1);
    /* margin-top: calc(var(--page-padding-top) * -1); */
    object-fit: fill;
    width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
    position: relative;
}

.banner_image {
  width: 100%;
  height: 47vh;
   display: block;
}

.banner_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.tracks_container {
    font-family: 'Georgia', serif;
  background-size: cover;
  background-position: center;
  /* padding: 2rem; */
  text-align: center;
  color: black;
}

.title {
  /* font-size: 2.5rem; */
  color: blue;
  font-weight: bold;
  /* margin-bottom: 1rem; */
}

.subtitle {
    text-align: left;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.separator {
  border: none;
  border-top: 1px solid black;
  margin: 1rem auto 2rem;
  width: 100%;
}

.tracks {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  gap: 2rem;
}

.track {
  width: 30%;
  min-width: 250px;
}

.trackTitle {
  color: red;
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 1rem;
}

.trackDesc {
  font-size: 1.2rem;
  color: blue;
  font-weight: bold;
  line-height: 1.5;
}

.item {
  margin-bottom: 1rem;
  line-height: 1.6;
}

.callforpapercontainer{
 font-family: 'Georgia', serif;
  background-size: cover;
  background-position: center;
  color: black;
}

.callforpapertitle {
    text-align: center;
  color: blue;
  font-weight: bold;
}

.description{
      font-size: 1.2rem;
  color: black;
  font-weight: bold;
  line-height: 1.5;
  margin-bottom: 20px;
}

.descriptionlist ul {
  list-style-type: disc;
  padding-left: 20px;
}

.item {
  margin-bottom: 10px;
  font-size: 1rem;
  line-height: 1.5;
  color: black;
}