.main_page {
  font-family: Arial, sans-serif;
  /* padding: 20px; */
  background-color: white;
}
.banner {
    height: 47vh;
    margin-bottom: var(--bottom-margin-between-contents);
    margin-left: calc(var(--page-margin-left) * -1);
    /* margin-top: calc(var(--page-padding-top) * -1); */
    object-fit: fill;
    width: calc(100% + var(--page-margin-left) + var(--page-margin-right));
    position: relative;
}

.banner_image {
  width: 100%;
  height: 47vh;
   display: block;
}

.banner_text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  font-size: 2rem;
  font-weight: bold;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}
.committee_container {
  font-family: 'Georgia', serif;
  max-width: 1000px;
  margin: 0 auto;
  padding: 20px;
  /* text-align: center; */
  line-height: 1.6;
}

.committee_container h2 {
  margin-top: 30px;
  font-size: 1.6rem;
  color: #2c3e50;
  text-transform: uppercase;
}

.committee_container h4 {
  color: blue;
}

.committee_container li {
  font-size: 1.0rem;
  color: black;
  margin: 5px 0;
  margin-left: 15px;
}
