.search_container {
  position: relative;
  display: flex;
  align-items: center;
}

.search_icon {
  font-size: 18px;
  cursor: pointer;
  color: #054D9F;
}

.search_input {
  width: 0;
  padding: 6px 10px;
  margin-left: 8px;
  opacity: 0;
  border: 1px solid #ccc;
  border-radius: 4px;
  transition: width 0.3s ease, opacity 0.3s ease;
  font-size: 14px;
  overflow: hidden;
}

.search_input.visible {
  width: 160px;
  opacity: 1;
}

.active_link {
  color: #FF5252 !important;
  /* border-bottom: 2px solid #007bff; */
  /* background-color: #fbaa19; */
  /* color: black; */
  /* color: white !important; */
}

.main_header a {
  position: relative;
  text-decoration: none;
  padding: 0 10px;
  display: flex;
  color: #054D9F;
  margin: 0 15px;
  text-decoration: none;
  font-weight: 500;
  align-items: center;
}

.main_header a:after {
  content: "";
  position: absolute;
  height: 3px;
  width: 0;
  background-color: #054D9F;
  transition: 0.3s;
}
.main_header a:after {
  right: 0;
  bottom: -10px;
}

.main_header a:hover {
  color: #FF5252;
}

.main_header a:hover:after,
.main_header a:hover:before {
  width: 100%;
}
